:root {
  /* Font Families */
  --tg-body-font-family: 'Plus Jakarta Sans', sans-serif;
  --tg-heading-font-family: 'Plus Jakarta Sans', sans-serif;
  --tg-icon-font-family: "Font Awesome 5 Free";

  /* Typography */
  --tg-body-font-size: 16px;
  --tg-body-line-height: 1.7;
  --tg-heading-line-height: 1.2;

  /* Colors */
  --tg-body-color: #4A5568;
  --tg-heading-color: #004958;

  /* Theme */
  --tg-theme-primary: #004958;
  --tg-theme-secondary: #007B83;
  --tg-theme-accent: #00B6B1;
  --tg-theme-highlight: #F5C66A;

  /* Backgrounds & Neutrals */
  --tg-color-white-default: #ffffff;
  --tg-color-gray-1: #E8F1F2;
  --tg-color-gray-2: #F4F7F7;
  --tg-color-gray-3: #E1EBEC;
  --tg-color-gray-4: #C7D3D4;
  --tg-color-dark: #1E2E2E;
  --tg-color-dark-2: #102020;

  /* Borders */
  --tg-border-1: #99B1B1;
  --tg-border-2: #C7D9D9;

  /* Font Weights */
  --tg-fw-extra-bold: 800;
  --tg-fw-bold: 700;
  --tg-fw-semi-bold: 600;
  --tg-fw-medium: 500;
  --tg-fw-regular: 400;
  --tg-fw-light: 300;
}


.btn-metallic {
  background: var(--tg-gradient-primary);
  color: white;
  box-shadow: var(--tg-shadow-soft);
  border: 1px solid var(--tg-border-3);
  transition: all 0.3s ease;
}

.btn-metallic:hover {
  background: var(--tg-gradient-gold);
  box-shadow: var(--tg-shadow-strong);
  transform: translateY(-2px);
}








/*=============================
	Typography css start
===============================*/
body {
  font-family: var(--tg-body-font-family);
  font-size: var(--tg-body-font-size);
  font-weight: var(--tg-fw-medium);
  color: var(--tg-body-color);
  line-height: var(--tg-body-line-height);
}

body.theme__gray {
  background-color: var(--tg-color-gray-3);
}

img,
.img {
  max-width: 100%;
  -webkit-transition: all 0.3s ease-out 0s;
  -moz-transition: all 0.3s ease-out 0s;
  -ms-transition: all 0.3s ease-out 0s;
  -o-transition: all 0.3s ease-out 0s;
  transition: all 0.3s ease-out 0s;
}

a,
button {
  color: var(--tg-theme-primary);
  outline: none;
  text-decoration: none;
  -webkit-transition: all 0.3s ease-out 0s;
  -moz-transition: all 0.3s ease-out 0s;
  -ms-transition: all 0.3s ease-out 0s;
  -o-transition: all 0.3s ease-out 0s;
  transition: all 0.3s ease-out 0s;
}

a:focus,
.btn:focus,
.button:focus {
  text-decoration: none;
  outline: none;
  -webkit-box-shadow: none;
  -moz-box-shadow: none;
  -ms-box-shadow: none;
  -o-box-shadow: none;
  box-shadow: none;
}

a:hover,
button:hover {
  color: var(--tg-theme-primary);
  text-decoration: none;
}

button:focus,
input:focus,
input:focus,
textarea,
textarea:focus {
  outline: 0;
}

h1,
h2,
h3,
h4,
h5,
h6 {
  font-family: var(--tg-heading-font-family);
  color: var(--tg-heading-color);
  margin-top: 0px;
  font-weight: var(--tg-fw-extra-bold);
  line-height: var(--tg-heading-line-height);
  text-transform: capitalize;
}

h1 a,
h2 a,
h3 a,
h4 a,
h5 a,
h6 a {
  color: inherit;
}

h1 {
  font-size: 2.5rem;
}

h2 {
  font-size: 2rem;
}

h3 {
  font-size: 1.75rem;
}

h4 {
  font-size: 1.5rem;
}

h5 {
  font-size: 1.25rem;
}

h6 {
  font-size: 1rem;
}

.list-wrap {
  margin: 0px;
  padding: 0px;
}

.list-wrap li {
  list-style: none;
}

p {
  margin-bottom: 15px;
}

hr {
  border-bottom: 1px solid var(--tg-common-color-gray);
  border-top: 0 none;
  margin: 30px 0;
  padding: 0;
}

label {
  color: var(--tg-heading-color);
  cursor: pointer;
  font-size: var(--tg-body-font-size);
  font-weight: var(--tg-fw-regular);
}

input[type=color] {
  appearance: none;
  -moz-appearance: none;
  -webkit-appearance: none;
  background: none;
  border: 0;
  cursor: pointer;
  height: 100%;
  width: 100%;
  padding: 0;
  border-radius: 50%;
}

*::-moz-selection {
  background: var(--tg-theme-primary);
  color: var(--tg-color-white-default);
  text-shadow: none;
}

::-moz-selection {
  background: var(--tg-theme-primary);
  color: var(--tg-color-white-default);
  text-shadow: none;
}

::selection {
  background: var(--tg-theme-primary);
  color: var(--tg-color-white-default);
  text-shadow: none;
}

/*=============================
    - Input Placeholder
===============================*/
input,
textarea {
  color: var(--tg-body-color);
}

*::-moz-placeholder {
  color: var(--tg-body-color);
  font-size: var(--tg-body-font-size);
  opacity: 1;
}

*::placeholder {
  color: var(--tg-body-color);
  font-size: var(--tg-body-font-size);
  opacity: 1;
}

textarea,
input[type=url],
input[type=email],
input[type=number],
input[type=text] {
  border: 1px solid var(--tg-border-4);
  -webkit-border-radius: 6px;
  -moz-border-radius: 6px;
  -o-border-radius: 6px;
  -ms-border-radius: 6px;
  border-radius: 6px;
  font-weight: 500;
  font-size: 14px;
  color: var(--tg-heading-color);
  background: var(--tg-color-gray-2);
  width: 100%;
  padding: 10px 16px;
  height: 45px;
  display: block;
}

textarea::placeholder,
input[type=url]::placeholder,
input[type=email]::placeholder,
input[type=number]::placeholder,
input[type=text]::placeholder {
  font-size: 14px;
  font-weight: 500;
  text-transform: capitalize;
}

textarea {
  min-height: 96px;
  max-height: 96px;
}

.select-grp {
  position: relative;
  z-index: 1;
}

.select-grp::after {
  content: "\f107";
  position: absolute;
  top: 50%;
  transform: translateY(-50%) rotate(180deg);
  font-family: "flaticon_tg_default";
  font-weight: 700;
  right: 0;
  font-size: 15px;
  color: var(--tg-body-color);
  line-height: 1;
}

.select-grp select {
  background-color: transparent;
  border: none;
  color: var(--tg-body-color);
  font-weight: 500;
  font-size: 15px;
  text-transform: capitalize;
  border-radius: 0;
  outline: none;
  padding: 5px 20px 5px 0;
  -webkit-appearance: none;
  -moz-appearance: none;
  appearance: none;
  width: 100%;
  line-height: 1.2;
  height: 40px;
  cursor: pointer;
  border-bottom: 1px solid #D8D8D8;
}

