/*=============================
	 Button style
===============================*/
.tg-btn {
  user-select: none;
  -moz-user-select: none;
  background: var(--tg-theme-primary) none repeat scroll 0 0;
  border: 0;
  color: var(--tg-color-white-default);
  cursor: pointer;
  display: inline-flex;
  align-items: center;
  font-size: 15px;
  font-weight: var(--tg-fw-bold);
  font-family: var(--tg-heading-font-family);
  letter-spacing: 0;
  line-height: 1.2;
  margin-bottom: 0;
  padding: 15px 26px 17px;
  text-align: center;
  text-transform: capitalize;
  touch-action: manipulation;
  -webkit-transition: all 0.3s ease-out 0s;
  -moz-transition: all 0.3s ease-out 0s;
  -ms-transition: all 0.3s ease-out 0s;
  -o-transition: all 0.3s ease-out 0s;
  transition: all 0.3s ease-out 0s;
  vertical-align: middle;
  -webkit-border-radius: 50px;
  -moz-border-radius: 50px;
  -o-border-radius: 50px;
  -ms-border-radius: 50px;
  border-radius: 50px;
  white-space: nowrap;
  overflow: hidden;
  --arrow-hover-move-x: 110%;
  --arrow-hover-move-y: 100%;
  gap: 10px;
}

.tg-btn svg {
  transition: all 0.3s ease-out 0s;
  width: 14px;
}

.tg-btn svg path {
  transition: transform 0.38s cubic-bezier(0.37, 0.08, 0.02, 0.93), opacity 0.18s ease-out;
}

.tg-btn svg path:nth-of-type(1) {
  transform: translateX(0) translateY(0);
  opacity: 1;
  transition-delay: 0.15s, 0.15s;
}

.tg-btn svg path:nth-of-type(2) {
  transform: translateX(calc(-1 * var(--arrow-hover-move-x))) translateY(var(--arrow-hover-move-y));
  opacity: 0.5;
  transition-delay: 0s, 0s;
}

.tg-btn:hover,
.tg-btn:focus-visible {
  background: var(--tg-theme-secondary);
  color: var(--tg-color-white-default);
}

.tg-btn:hover svg path:nth-of-type(1),
.tg-btn:focus-visible svg path:nth-of-type(1) {
  transform: translateX(var(--arrow-hover-move-x)) translateY(calc(-1 * var(--arrow-hover-move-y)));
  opacity: 0;
  transition-delay: 0s, 0s;
}

.tg-btn:hover svg path:nth-of-type(2),
.tg-btn:focus-visible svg path:nth-of-type(2) {
  transform: translateX(0) translateY(0);
  opacity: 1;
  transition-delay: 0.15s, 0.15s;
}

.tg-btn.white-btn {
  background: var(--tg-color-white-default);
  color: var(--tg-theme-secondary);
}

.tg-btn.white-btn:hover {
  background: var(--tg-theme-primary);
  color: var(--tg-color-white-default);
}

.tg-btn.black-btn {
  background: var(--tg-theme-secondary);
  color: var(--tg-color-white-default);
}

.tg-btn.black-btn:hover {
  background: var(--tg-theme-primary);
}

.tg-text-btn {
  text-transform: capitalize;
  color: var(--tg-theme-secondary);
  display: inline-flex;
  align-items: center;
  font-weight: 600;
  font-size: 16px;
  gap: 10px;
  white-space: nowrap;
  overflow: hidden;
  --arrow-hover-move-x: 110%;
  --arrow-hover-move-y: 100%;
}

.tg-text-btn svg {
  transition: all 0.3s ease-out 0s;
  width: 14px;
}

.tg-text-btn svg path {
  transition: transform 0.38s cubic-bezier(0.37, 0.08, 0.02, 0.93), opacity 0.18s ease-out;
}

.tg-text-btn svg path:nth-of-type(1) {
  transform: translateX(0) translateY(0);
  opacity: 1;
  transition-delay: 0.15s, 0.15s;
}

.tg-text-btn svg path:nth-of-type(2) {
  transform: translateX(calc(-1 * var(--arrow-hover-move-x))) translateY(var(--arrow-hover-move-y));
  opacity: 0.5;
  transition-delay: 0s, 0s;
}

.tg-text-btn:hover,
.tg-text-btn:focus-visible {
  color: var(--tg-theme-primary);
}

.tg-text-btn:hover svg path:nth-of-type(1),
.tg-text-btn:focus-visible svg path:nth-of-type(1) {
  transform: translateX(var(--arrow-hover-move-x)) translateY(calc(-1 * var(--arrow-hover-move-y)));
  opacity: 0;
  transition-delay: 0s, 0s;
}

.tg-text-btn:hover svg path:nth-of-type(2),
.tg-text-btn:focus-visible svg path:nth-of-type(2) {
  transform: translateX(0) translateY(0);
  opacity: 1;
  transition-delay: 0.15s, 0.15s;
}

.tg-btn-two {
